/* clear float */
.login .top-login{
    width: 130px;
    height: 130px;
    display: block;
    position: relative;
    /* left:50%; */
    margin: 0 auto;
    /* -webkit-transform: rotate(45deg);
    transform-origin: -65px 0; */
    /* margin: 0 auto 4em; */
    /* background: #fff; */
    text-align: center;
}
.top-login span{
    border: 2px solid #F36;
    width: 105px;
    height: 105px;
    display: block;
    margin: 0px auto;
    position: absolute;
    top: 11px;
    left: 11px;
}
.top-login span img{
  display: block;
  width: 64px;
  height: 64px;
    /* -webkit-transform: rotate(-45deg) translate3d(0, 0, 0);
    -moz-transform: rotate(-45deg) translate3d(0, 0, 0);
    -ms-transform: rotate(-45deg) translate3d(0, 0, 0);
    -o-transform: rotate(-45deg) translate3d(0, 0, 0);
    transform: rotate(-45deg) translate3d(0, 0, 0); */
  margin: 20.5px;
}
.login h1{
  margin: 20px 0;
  color: white;
}
/*----*/
.login-top{
	width: 90%;
  display: block;
  margin: 0 auto;
  margin-top: 20px;
}
.login-ic {
  background: rgba(255, 255, 255, 0.32); 
	margin-bottom:1.5em;
  padding: 8px; /* Safari */
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
}
.login-ic i {
    background: url(data:image/png;base64,)no-repeat 6px 6px;
    width: 38px;
    height: 38px;
	/*-- agileits --*/
    display: inline-block;
}
.login-ic i.icon {
    background: url(data:image/png;base64,)no-repeat 6px 6px;
}
.login-ic input[type="text"],.login-ic  input[type="password"] {
    background: none;
    outline: none;
    font-size: 15px;
    font-weight: 400;
    color: #fff;
    padding: 10px 16px;
    border: none;
    border-left: 1px solid #fff;
    width: 80%;
    display: inline-block;
    margin-left: 7px;
}
.log-bwn {
  width: 100%;
  text-align: center; /* Safari */
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  -webkit-box-pack:justify;
      -ms-flex-pack:justify;
          justify-content:space-between;
}
.log-bwn input[type="submit"] {
  font-size: 15px;
  font-weight: 700;
  color: #fff;
  padding: 13px 0;
  background: #ff3366;
  display: inline-block;
  -webkit-box-flex:1;
      -ms-flex-positive:1;
          flex-grow:1;
  width: 100%;
	outline:none;
	border:2px solid #ff3366;
	cursor:pointer;
	text-transform:uppercase;
}
.log-bwn input[type="submit"]:hover{
	transition: all 0.3s ease;
  -webkit-transition: all 0.3s ease;
  -moz-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
	border:2px solid #fff;
}
.login p{
  color: white;
  margin-top: 20px;
}
.login p.copy a{
	color:#fff;
	text-decoration:none;
}
.login p.copy{
  font-size: 20px;
  /* word-wrap:break-word;  
  word-break:break-all;   */
}
.login p.copy a:hover{
	text-decoration:underline;
}
.login .rutrueInfo{
  color: red;
  font-size: 30px;
}
.login p.des{
  padding: 0 5%;
  text-align: left;
  font-style: italic;
  line-height: 30px;
  /* text-indent: -2em;
  padding-left: 2em; */
}
